        .cajon.color-primario.p-4.mb-4.mb-lg-0
          p.mb-0 La comunicacin del color es subjetiva; para percibir el color, el ojo humano requiere de tres elementos: luz, visin y objeto, si no existen estos tres, no se podr percibir el color. La medicin de color hace nfasis en la definicin de los esquemas numricos, con los cuales comunican dicha informacin electrnicamente, potenciando el control de la impresin.
