        h3 El color y sus componentes
    p.mb-5 Es importante contar con tres componentes bsicos para que el color exista: luz, visin y objeto. A continuacin, profundice en aspectos relevantes y fundamentales sobre estos elementos y su relacin con el color:
    .row.bg-color4.mb-5
      .col-12.px-5
        SlyderE(indicadores="izquierda").my-4
          .row
            .col-md-6.mb-4.mb-md-0
              h3.mt-5.txt-deco1 Luz, visin y objeto
              p La interaccin de estos elementos da sentido a un color proyectado, permitiendo distinguirlo. Las ondas electromagnticas cuentan con distintas longitudes y, en el momento en que los picos de onda se encuentran cerca, tendrn una longitud de onda corta; por el contrario, si los picos  de onda se encuentran separados, las longitudes de onda sern ms largas.
            .col-md-6
              .p-1
                figure
                  img(src='@/assets/curso/tema1/img5.png')
          .row
            .col-md-6.mb-4.mb-md-0
              h3.mt-5.txt-deco1 En impresin
              p Los materiales derivados del papel son impresos en prensas flexogrficas y son reflectores de luz; los pigmentos y colorantes de las tintas empleadas en impresin se disean como absorbentes altamente efectivos de fragmentos determinados de la energa de la luz (mientras irradian, a la vez, la energa restante). Cada uno de los modelos fsicos cuenta con su propia curva de reflectancia (cada curva de reflectancia es nica).
            .col-md-6
              figure
                img(src='@/assets/curso/tema1/img6.png')
          .row
            .col-md-6.mb-4.mb-md-0
              h3.mt-5.txt-deco1 Realidad vs percepcin ocular
              p Comprender los conceptos de la luz y el color es un ejercicio complicado, ya que, cuando estos se observan, en realidad lo que el ojo humano percibe es una parte muy pequea del espectro electromagntico, a esto se le conoce como rango visible.
            .col-md-6
              figure
                img(src='@/assets/curso/tema1/img7.png')
          .row
            .col-md-6.mb-4.mb-md-0
              h3.mt-5.txt-deco1 Espectro visible
              p En las mediciones del color, este, a menudo, se caracteriza por la longitud de onda, y as el espectro visible vara desde la luz roja (longitud de onda larga) hasta la luz violeta (longitud de onda corta), haciendo posible, entre ellas, todos los colores que el ojo humano percibe.
            .col-md-6
