        h3.text-center Colormetro
        p.text-center Instrumento basado en filtros. Contiene tres filtros para separar la luz roja, azul y verde y est diseado para adaptarse a la respuesta del ojo humano. La luz pasa a travs de estos filtros y, cuando alcanza el sensor, los tres valores de color corresponden a lo que percibe el ojo humano.
      .tarjeta.color-acento-botones.p-4
        .row.justify-content-center.mb-3
