        p.text-center Funciona a la inversa, dividiendo la luz en longitudes de onda individuales (al igual que la luz blanca se divide en sus propias longitudes de onda cuando brilla a travs de un prisma, crea un arcoris).
        br.mb-3
      .tarjeta.color-acento-botones.p-4
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/tema1/img6.svg' alt='AvatarTop')
