    #t_1_1
    .titulo-segundo.color-secundario
      h2 1.1  Sistemas de medicin
    .row.mb-5
      .col-lg-8.mb-4.mb-lg-0
        p La colorimetra primaria se basa en la comparacin con estndares de color existentes. El ojo humano es un indicador de color muy individual, por ejemplo, para determinar si algo es verde o si, en cambio, es ligeramente amarillo. #[span.txt-deco1 El color reproducible es indistinguible si no se mide en condiciones especficas. El rango de longitud de onda visible es de aproximadamente 380-740 nm, mientras que el ojo humano no puede ver los rayos ultravioleta e infrarrojos].
        p.mb-5 Ample sus conocimientos, en lo referente a sistemas de medicin, explorando con atencin el recurso que se le presenta a continuacin:
        LineaTiempoD.color-secundario
          p.text-small(numero="1" titulo="Modos de color") Existen algunas definiciones de modos de color, tal como RGB (coloracin aditiva) o CMYK (coloracin sustractiva), no solo tratan del color en s, tambin hay cuantificaciones que se encargan de describir las diferencias entre: luminosidad, saturacin y brillo. 
          p.text-small(numero="2" titulo="Normatividad asociada al color") Para seales de trfico y seguridad, por ejemplo, existe la norma DIN EN ISO 7010, la cual determina los colores que se utilizarn. Existen muchos estndares con definiciones de color para la mayora de los productos.
          p.text-small(numero="3" titulo="Control de calidad de productos") En caso de comparar dos productos de la industria para comprobar que tengan el 100% del mismo color, se necesita una tcnica de medicin precisa para comprobar si los colores son repetibles; para ello, es importante realizar un control de calidad con el fin de obtener acertadamente la medicin del color.
          p.text-small(numero="4" titulo="En procesos de limpieza") Si se inicia un paso de limpieza (CIP), un sensor de color puede identificar rpidamente si la muestra de color an est presente en la lnea de produccin o si las tuberas y los recipientes estn limpios.
