        p Cuando se tiene conocimiento del proceder de la muestra, se podr elegir la longitud de la onda con el fin de establecer el sensor del tono. En caso de no tener claridad de cul ser la longitud de onda a seleccionar, es importante efectuar un escaneo de extensin de onda VIS con un espectrofotmetro especializado del espectro. #[span.txt-deco1 Se puede elegir la mejor longitud de onda para distinguir la muestra de otras fracciones].

