    p.mb-5 Las siguientes son las escalas de color con sus respectivas definiciones:
    .row.mb-5
      .col-lg-8.mb-4.mb-lg-0
        TabsA.color-acento-botones
          .tarjeta.color-acento-botones--borde.p-4(titulo="Escala de color de Saybolt")
            h4 Escala de color de Saybolt
            p Con el fin de poder comprobar si el combustible, gasolina, queroseno o diesel, presenta algn tipo de contaminacin o degradado por su perodo de almacenamiento, es viable hacer una prueba de medicin de color concreta (escala de color Saybolt ASTM D156, ASTM D6045), la escala de colores de Saybolt vara de 30 (agua clara / blanco) a -16 (amarillo oscuro).
          .tarjeta.color-acento-botones--borde.p-4(titulo="Escala APHA")
            h4 Escala APHA
            p Para valorar el nivel de contaminacin en aguas residuales y monitorear aguas potables, se utiliza una escala colorimtrica especfica, conocida como escala Pt / Co, escala APHA-Hazen o escala APHA. Las pruebas de color APHA se realizan en industrias qumicas, farmacuticas, petroleras y del plstico, as como en la industria de bebidas.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Color Gardner")
            h4 Color Gardner
            p Esta escala de colores describe el color amarillo que presenta un lquido transparente, el cual vara de 1 a 18, siendo 1 amarillo claro y 18 marrn oscuro. La norma ASTM D 1544 (mtodo de prueba estndar para el color de lquidos claros) describe un filtro de vidrio, en comparacin con un lquido amarillo, y la norma ASTM D6045 define la relacin con un colormetro Gardner.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Color CIELAB")
            h4 Color CIELAB
            p El espacio de color L*a*b* (CIELAB, CIEL*a*b*, Lab colors) hace una descripcin detallada de todos los colores visibles, manejando un rea tridimensional. All la luminosidad L* se ubica verticalmente sobre la capa de color (a*, b*), dicha escala de colores se representa en EN ISO 11664-4 Colorimetra CIE 1976 L*a*b* Espacio de color.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Mtodo ICUMSA")
            h4 Mtodo ICUMSA
            p Dentro del libro de mtodos ICUMSA, se podrn encontrar instrucciones precisas con el fin de analizar todo tipo de azcares, melazas y otros obteniendo la evaluacin del color, la extincin se examina a 420 nm a lo largo de una ruta ptica especfica. El azcar blanco puro absorbe menos luz (valor 45) que el azcar moreno en alta absorcin (valor 1000).
          .tarjeta.color-acento-botones--borde.p-4(titulo="Clculo valor ICUMSA")
            h4 Clculo valor ICUMSA
            p.mb-0 Para calcular el valor ICUMSA, se deben verificar parmetros adicionales, como la densidad y el ndice de refraccin.
            figure.my-4
              img(src='@/assets/curso/tema1/img10.svg', style="width: 300px").m-auto
            p E (420): extincin medida a 420 nm
            p d: longitud del camino ptico (cm)
            p D: densidad
            p Bx: ndice de refraccin (p. ej. BRIX)
