    .titulo-segundo.color-secundario
      h2 1.2  Densitmetro y espectrofotmetro
    .row.mb-5(data-aos="zoom-in")
      .col-lg-4
        p La colorimetra es una ciencia que trata de medir y cuantificar los colores mediante nmeros, para operar con ellos y deducir caractersticas de aquellos colores obtenidos mediante mezclas, a partir de tres colores considerados como primarios.
      .col-lg-8
        .row
          .col-lg-4.bg-color5
          .col-lg-8.bg-color6
            .p-4
              p #[span.txt-deco1 La colorimetra permite saber la cantidad de color primario que se debe mezclar para obtener otros].  Asimismo, estudia aspectos fsicos del color, como la longitud de onda dominante (tono), la pureza de la excitacin (saturacin) y luminancia (brillo de los cuerpos que emiten luz y claridad de los cuerpos que reflejan la luz).
    p.mb-5 Estas son algunas generalidades que usted debe conocer sobre el densitmetro:
    TabsA.color-acento-botones.mb-5
      .tarjeta.color-acento-botones--borde.p-5(titulo="Densidad ptica o grado de oscuridad")
        .row.align-items-center
          .col
            h4 Densidad ptica o grado de oscuridad
            p El densitmetro es un dispositivo con el cual se puede medir la densidad ptica o grado de oscuridad en materiales semitransparentes o con superficies reflejantes; tambin se le conoce como espectrodensitmetro.
          .col-auto
            img(src='@/assets/curso/tema1/img20.svg', style="width: 100px").m-auto
      .tarjeta.color-acento-botones--borde.p-5(titulo="Emisin y reflexin de luz")
        .row.align-items-center
          .col
            h4 Emisin y reflexin de luz
            p El proceso de densitometra hace nfasis en la emisin de luz a un punto especfico de la superficie que se encuentre en anlisis. Esto se logra a travs de una serie de lentes que registran los niveles de emisin y reflexin de la luz.
          .col-auto
            img(src='@/assets/curso/tema1/img21.svg', style="width: 100px").m-auto
      .tarjeta.color-acento-botones--borde.p-5(titulo="Interpretacin de los valores")
        .row.align-items-center
          .col
            h4 Interpretacin de los valores
            p La luz resultante es captada por una serie de lectores encargados de interpretar, electrnicamente, los valores. Los resultados obtenidos se comparan con algunas otras referencias y la medicin arrojar un resultado final, el cual se reflejar en la pantalla principal.
          .col-auto
            img(src='@/assets/curso/tema1/img22.svg', style="width: 100px").m-auto
    p.mb-5 Las principales lecturas densitomtricas son las siguientes:
