                p.text-center #[b Ganancia de punto]
                p.text-center Hace referencia a valores establecidos en las ganancias mecnica y ptica, los cuales generan un punto de mayor dimensin de la que tena, tambin podra entenderse como imgenes oscuras que tienen un alto nivel de tono.
