            .col-12.bg-botton
              .p-4
                p.text-center #[b Atrape]
                p.text-center Atrape que hace una tinta sobre otra que est recientemente impresa, es decir, cuando ambas tintas estn frescas, esto lo afecta el tono del impreso y por lo general tiende a confundir a un operario, ya que, al momento de observar un impreso, el operario no diferencia con claridad qu tono impreso es. Ello puede generar reprocesos.
      .col-10.col-lg-4
        .p-2
          .row
            .col-12.bg-top
