            .col-12.bg-botton
              .p-4
                p.text-center #[b Balance de grises]
                p.text-center Al momento de realizar una combinacin de colores CMYK con todos los valores iguales, se puede producir un gris neutro; cualquier defecto en uno de estos colores durante el proceso de cuatricroma, dar como resultado un color azulado, rojizo, amarillento o gris verdoso, que podr medirse con un densitmetro.
      .col-10.col-lg-4
        .p-2
          .row
            .col-12.bg-top
