                p.text-center #[b Error de matiz y grisura de las tintas]
                p.text-center Generalmente, las tintas CMY generan cierto porcentaje de error de matiz (de cian, est contaminado con rojo, magenta muy contaminado con verde, y amarillo, ligeramente con rojo tambin). Al medir una de estas tintas con un densitmetro, arrojar un valor determinado para cada componente mezclado.
