                p.text-center Si se encuentran valores ms altos de matiz o grisura de las tintas en otro lote impreso, se puede saber con certeza que el tono de la impresin ser diferente, lo que resultar en una mala calidad de impresin y posiblemente la decisin de devolver el producto.
    .row.mb-5
