          h3 Tipos y usos de densitmetros
    p.mb-5 Principalmente, se encuentran dos tipos de densitmetros:
    .row.bg-color8
      .col-12.px-5
        .p-4
          .row.justify-content-evenly
            .col-lg-4.bg-color7.mb-4.mb-lg-0
